import React from 'react';
import { formatDate } from '@transferwise/formatting';

/**
 *
 * @param {Array} options
 * @param {string|number} value
 */
const getValueLabel = (options, value) => {
  const option = options.find(currentOption => currentOption.value === value);
  return option && option.label ? option.label : value;
};

/**
 *
 * @param {string} value
 */
const mask = value => new Array(value.length + 1).join('*');

const DefinitionListItem = ({ field, model, propKey, locale }) => {
  const fieldValue = model[propKey];
  switch (field.control) {
    case 'select':
    case 'radio':
      return <span>{getValueLabel(field.values, fieldValue)}</span>;
    case 'date':
      return (
        <span>
          {formatDate(fieldValue instanceof Date ? fieldValue : new Date(fieldValue), locale)}
        </span>
      );
    case 'number':
      return <span>{fieldValue}</span>;
    case 'password':
      return <span>{mask(fieldValue)}</span>;
    case 'file':
      return (
        <div className="thumbnail">
          <img alt={field.title} src={fieldValue} />
        </div>
      );
    default:
      return <span>{fieldValue}</span>;
  }
};

export default DefinitionListItem;



