import React, { PureComponent, Fragment } from 'react';
import Types from 'prop-types';

import DefinitionListItem from './DefinitionListItem';
import { prepFields } from './api/requirements';

class DefinitionList extends PureComponent {
  static propTypes = {
    model: Types.shape({}).isRequired,
    fields: Types.shape({}).isRequired,
    locale: Types.string,
    title: Types.string,
    narrow: Types.bool,
    layout: Types.string,
  };

  static defaultProps = {
    locale: 'en-GB',
    title: null,
    narrow: false,
    layout: 'vertical',
  };

  state = {
    model: this.props.model,
    fields: this.props.fields,
    locale: this.props.locale,
    title: this.props.title,
    narrow: this.props.narrow,
    layout: this.props.layout,
    isVertical: this.props.layout === 'vertical',
  };

  componentWillMount() {
    this.setState({
      fields: prepFields(this.state.fields, this.state.model),
    });
  }

  getClassNameForLine() {
    if (this.state.layout === 'justified') {
      return 'dl-horizontal-item';
    } else if (this.state.layout === 'horizontal') {
      return 'dl-horizontal';
    }
    return null;
  }

  render() {
    return (
      <Fragment>
        {this.state.title && (
          <div className="m-t-1">
            <h4 className="page-header p-t-3">{this.state.title}</h4>
          </div>
        )}

        <div className={this.state.isVertical ? 'row row-equal-height' : null}>
          {Object.keys(this.state.fields).map((key, index) => {
            const field = this.state.fields[key];

            if (!(this.state.model[key] && !field.hidden)) {
              return null;
            }

            return (
              <div
                className={
                  !this.state.narrow && this.state.isVertical ? 'col-xs-12 col-sm-6' : null
                }
                // eslint-disable-next-line react/no-array-index-key
                key={index}
              >
                {!field.group && (
                  <dl className={this.getClassNameForLine()}>
                    <dt>{field.title}</dt>
                    <dd className="text-word-break">
                      <DefinitionListItem
                        field={field}
                        propKey={key}
                        model={this.state.model}
                        locale={this.state.locale}
                      />
                    </dd>
                  </dl>
                )}

                {field.group && (
                  <dl className={this.getClassNameForLine()}>
                    <dt>{field.title}</dt>
                    <dd className="text-word-break">
                      {field.group.map((fieldSection, fieldSectionIndex) => (
                        <DefinitionListItem
                          field={fieldSection}
                          // eslint-disable-next-line react/no-array-index-key
                          key={fieldSectionIndex}
                          propKey={key}
                          model={this.state.model}
                          locale={this.state.locale}
                        />
                      ))}
                    </dd>
                  </dl>
                )}
              </div>
            );
          })}
        </div>
      </Fragment>
    );
  }
}

export default DefinitionList;



